#capes-barra{
  background-color: @capes-barra-bg;
  color: @capes-barra-text-color;

  #capes-barra-logo{
    display: block;
    padding: 2px 0;
    svg.svg-capes-logo-vertical{
      height: @capes-barra-logo-capes-height;
      fill: @capes-barra-logo-capes-color;
    }
  }

  #capes-barra-links
  , #capes-barra-acessibilidade{
    margin: 0;
    ul{
      display: table;
      li{
        list-style: none;
        padding: 0;
        margin: 0;
        float: left;

        a{
          color: @capes-barra-text-color;
          display: block;
        }
      }
    }
  }

  #capes-barra-links{
    margin: 0;
    font-size: .8em;
    ul{
      padding-top: 12px;
      li{
        list-style: none;
        padding: 0;
        margin: 0;
        border-left: 1px solid @capes-barra-text-color;
        float: left;
        &:first-child{border-left: none};

        a{
          color: @capes-barra-text-color;
          display: block;
          margin: 5px 10px;
          text-align: center;
        }
      }
    }
  }

  #capes-barra-acessibilidade{
    ul{padding-top: 17px}
    span{
      font-size: .8em;
      display: inline-block;
      text-align: right;
      float: left;
      line-height: 1em;
      padding-top: 2px;
    }

    a{
      float: left;
      margin-left: 5px;
      display: block;
      padding: 0;
      font-size: 1.8em;
      line-height: .75em;
    }

    .contrast{
      width: 91px;
      span{width: 55px;}

      a{
        color: @capes-barra-acessibilidade-contraste-btn-color;
        background-color: @capes-barra-acessibilidade-contraste-btn-bg;

        &:hover{color: darken(@capes-barra-acessibilidade-contraste-btn-color, 10%)}
      }
    }

    .fonte{
      span{width: 60px}
      ul{margin: 0;padding: 0}
      a:hover{
        color: darken(@capes-barra-text-color, 30%);
      }
    }
  }

}