#sistema-footer{
  .container{
    padding-top: 10px;
    padding-bottom: 10px;
  }

  svg.svg-sistema-logo{
    fill: @sistema-footer-logo-color;
    height: @sistema-footer-logo-height;
    width: @sistema-footer-logo-width;
  }

  p{
    font-size: .75em;
    line-height: 1.5em
  }

  .sistema-assinaturas{
    text-align: right;

    .sistema-assinatura-capes
    , .sistema-assinatura-mec
    , .sistema-assinatura-gov{display: inline-block}
    
    .sistema-assinatura-capes{
      svg.svg-capes-logo-vertical{
        height: @sistema-footer-logo-capes-height;
        width: auto;
        .capes-logo-65-verde-escuro{fill: @capes-logo-65-verde-escuro}
        .capes-logo-65-verde{fill: @capes-logo-65-verde}
        .capes-logo-65-azul{fill: @capes-logo-65-azul}
        .capes-logo-65-amarelo{fill: @capes-logo-65-amarelo}
      }
      svg.svg-capes65-logo-horizontal{
        height: @sistema-footer-logo-capes-height;
        width: auto;
      }
    }
    .sistema-assinatura-mec{
      svg.svg-mec-logo{
        height: @sistema-footer-logo-mec-height;
        width: auto;
        margin: 3px 7px;
      }
    }
    .sistema-assinatura-gov{
      svg.svg-gov-logo{
        height: @sistema-footer-logo-gov-height;
        width: auto;
        margin-right: -7px;
      }
    }
  }
}
.bg{
    background-color: @sistema-footer-bg;
    padding: 15px;
  }
  
// Rodape - Governo Federal
#footer-brasil {
  background: none repeat scroll 0% 0% @footer-brasil-bg;
  padding: 1em 0px;
  max-width: 100%;
}