#sistema-header{
  background-color: @sistema-header-bg;

  .navbar-default{
    border: none;
    background-color: @sistema-header-bg;
    margin: 15px auto;

    h1.navbar-brand{
      margin: 0;
      padding: 0;

      span{
        color: @sistema-color-primary;
        display: block;
        font-size: 1.4em;
        font-weight: bold;
      }
      small{
        color: @sistema-color-secundary;
      }

      span, small{display: none}

      svg.svg-sistema-logo{
        fill: @sistema-header-logo-color;
        height: @sistema-header-logo-height;
        width: @sistema-header-logo-width;
      }

      a:hover{
        span{color: darken(@sistema-color-primary,10%)}
        small{color: darken(@sistema-color-secundary,10%)}
        svg.svg-sistema-logo{fill: darken(@sistema-header-logo-color,10%)}
      }
    }

    .navbar-nav{
      margin-top: 20px;

      >li{
        border-left: 1px solid @sistema-nav-main-border-color;
        &:last-child{border-left: none}
      }
      a{
        padding: 10px 15px;
        color: @sistema-nav-main-link-color;
        font-size: .8em;
        font-weight: bold;
        line-height: 1em;

        &.btn{
          border: 1px solid @sistema-nav-main-border-color;
          border-radius: 0;
        }

        &:hover{
          background-color: darken(@main-bg,3%);
        }
      }

    }

  }

  #sistema-nav-main{
    color: @sistema-nav-main-link-color;
  }
}